*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: Roboto-Thin;
    src: url(./fonts/Roboto-Thin.ttf);
}

@font-face {
    font-family: Roboto-Regular;
    src: url(./fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: Roboto-Medium;
  src: url(./fonts/Roboto-Medium.ttf);
}

html{
    font-size: 10px;
}

body{
    font-family: Roboto-Thin;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

/* universal styles */
a{
    color: white;
    text-decoration: none;
}
/* End of universal styles */

/* Hero component */
.hero{
    height: 80vh;
    background-image: linear-gradient(0deg, rgba(14, 24, 9, 0.7), rgba(14, 24, 9, 0.7)), url(../src/img/hero.jpg);
    background-position: center;
    background-size: cover;
    padding-top: 50px;
}

.navbar{
    display: flex;
    justify-content: space-around;
    font-size: 1.6rem;
    font-family: Roboto-Regular;
    width: 80%;
    margin: 0 auto;
    align-items: center;
}

.navbar__logo{
  width: 50px;
  background-color: white;
  border-radius: 50%;
  border: 2px solid #00b41e;
}

.navbar__logo img{
  width: 100%;
}
.navbar .navbar__links{
    width: 65%;
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    margin: 0;
}

.hero__text-contents-wrapper{
  width: 80%;
  display: flex;
}

.hero_text-contents__whitespace{
  width: 40%;
}

.hero__text-contents__main{
  width: 60%;
}

.hero__text-contents__main__secondary-btns{
    display: flex;
    list-style: none;
    font-size: 2rem;
    margin-right: 10px;
    margin-top: 50px;
    align-items: center;
    letter-spacing: 3px;
    margin-top: 100px;
    margin-bottom: 40px;
}

.hero__text-contents__main__secondary-btns li{
  margin-right: 30px;
  font-family: Roboto-Medium;
  font-weight: bolder;
}


.hero__text-contents__main__secondary-btns li:first-child{
  background-color: #00b41e;
  padding: 10px 20px;
  border-radius: 20px;
}

.hero__text-contents__main h1{
  font-size: 3.5rem;
  font-family: Roboto-Medium;
  font-weight: bold;
  color: white;
  margin: 20px 0;
  letter-spacing: 5px;
}

.hero__text-contents__main__primary-btns{
  display: flex;
  list-style: none;
  font-size: 1.5rem;
  margin-right: 10px;
  margin-top: 30px;
  align-items: center;
  letter-spacing: 3px;
  margin-top: 50px;
  font-family: Roboto-Regular;
}

.hero__text-contents__main__primary-btns li{
  display: flex;
  list-style: none;
  margin-right: 10px;
  letter-spacing: 3px;
  padding: 15px 20px;
  border-radius: 15px;
  font-weight: bold;
  background-color: #00b41e;
}
/* End of Hero component */

/* Figures component */
.figures{
  background: #00b41e;
  height: 20vh;
  display: flex;
  color: white;
  text-align: center;
  font-family: Roboto-Medium;
  justify-content: space-between;
  align-items: center;
  padding: 0 250px;
  margin-bottom: 50px;
}

.figures img{
  width: 40px;
  margin: 10px;
}

.figures h3{
  font-size: 1.6rem;
}

.figures h4{
  font-size: 3rem;
  font-weight: bolder;
}
/* End of Figures component */

/* Blog component */
.blog-wrapper{
  width: 90%;
  margin: 0 auto;
}

.blog{
  width: 100%;
  display: flex;
  justify-content: space-around;
  font-family: Roboto-Regular;
}

.blog .blog-cards {
  width: 65%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.blog .blog-sidebar {
  width: 25%;
}

.blog .blog-cards .card{
  border-bottom: 0 none;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
  width: 30%;
  background: white;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}

.blog .blog-cards .card-body{
  padding: 10px;
}

.card-body h3{
  font-size: 1.6rem;
}

.blog .blog-cards .card-body .location{
  color: #00b41e;
  margin: 10px 0;
  font-size: 2rem;
}

.blog .blog-cards .card-body .location span img{
  width: 15px;
}

.blog .blog-cards .card-body .date{
  font-weight: bold;
  font-size: 1.5rem;
}

.cards-row .card-img {
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 200px;
}

.card-img img{
  width: 100%;
  height: 100%;
}


.opportunity{
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
  border-radius: 2px;
  text-align: center;
}

.opportunity h3{
  padding: 10px;
  margin: 6px 0;
  font-size: 15px;
}

.opportunity h3 span img{ 
  width: 10px;
}


.see-all img{
width: 20px;
}

/* End of Blog component */

/* Partners component */
.partners{
  width: 80%;
  margin: 40px auto;
  margin-bottom: 170px;
}

.partners h3{
  font-size: 2rem;
  text-align: center;
  color: #00b41e;
  margin: 20px;
}

.partners-wrapper{
  display: flex;
  justify-content: space-between;
}

.partner{
  width: 130px;
  height: 50px;
  margin-right: 15px;
}

.partner img{
  width: 100%;
}

/* End of Partners component */

/* Social component */
.social-media-section{
  /* width: 95%;
  margin: 0 auto; */
  margin-bottom: 50px;
}

.social-media-section h3{
  color: #00b41e;
  text-align: center;
  margin: 70px;
  font-size: 2rem;
}

.social-media-section .social-media-cards{
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
}

.social-media-section .social-media-cards .card{
  width: 30%;
  height: 170px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  position: relative;
  padding-top: 30px;
  margin-bottom: 50px;
  font-size: 1.8rem;
}

.social-media-section .social-media-cards .card .logo{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  position: absolute;
  top: -32px;
  left: 110px;
  z-index: 100;
  overflow: hidden;
}

.social-media-section .card .logo img{
  width: 100%;
}

.social-media-section .card p{
  text-align: center;
  line-height: 25px;
}
/* End of social component */

/* Newsfeed */
.newsfeed-container{
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

.newsfeed-container h2{
  color:#00b41e;
  margin-bottom: 20px;
}

.newsfeed{
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  padding: 40px 30px;
  border-radius: 2px;
}

.newsfeed h3{
  line-height: 30px;
  text-align: center;
  margin-bottom: 10px;
  font-size: 15px;
}

/* End of newsfeed */

/* Quicklinks component */
.quicklinks{
  height: 600px;
  background: linear-gradient(0deg, rgba(14, 24, 9, 0.9), rgba(14, 24, 9, 0.9)), url(./img/hero.jpg);
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.quicklinks h2{
  text-align: center;
  margin: 20px 0;
  color: white;
}

.quicklinks .links{
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
}

.quicklinks .links .link{
  color: white;
  text-align: center;
}

.link p{
  font-size: 17px;
  margin: 20px 0;
}


.quicklinks .profile{
  display: block;
  width: 160px;
  color: white;
  padding: 15px;
  margin: 10px auto;
  background-color:#00b41e;
  font-size: 1.5rem;
  font-weight: bolder;
  font-family: Roboto-Regular;
  text-align: center;
}

.quicklinks h3{
  text-align: center;
  color: white;
  margin: 40px 0;
  letter-spacing: 3px;
  font-size: 1.5rem;
}

.quicklinks .social-icons{
  display: flex;
  justify-content: center;
}

.quicklinks .social-icons img{
  width: 25px;
  text-align: center;
  margin: 10px 10px;
}
/* End of Quicklinks */

/* Footer Component */
    
footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #00b41e;
  font-size: 2rem;
  font-weight: 900;
}

footer .col{
  width: 50%;
  text-align: center;
  color: white;
  padding: 20px;
  font-size: 16px;
}

footer .col:first-child{
  border-right: 2px solid white;
}    

/* End of Footer component */